body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

*{font-family:'KoPub Dotum', fantasy}.home{background-color:#023E72;width:100%;height:100vh;display:flex;align-items:center;flex-direction:column;justify-content:center;color:white;font-size:5vw}@media screen and (min-width: 1920px){.home{font-size:6em}}.home>:nth-child(1){letter-spacing:0.38em}.home>:nth-child(2){letter-spacing:0.25em}.home>:nth-child(3){letter-spacing:0.14em}

*{font-family:'KoPub Dotum', fantasy}nav{width:100%;min-height:64px;background-color:#05232E;display:flex;align-items:center;justify-content:center}ul{width:100%;max-width:1200px;margin:0}@media screen and (max-width: 920px){ul{padding:1em 0;text-align:center}}li{color:white;margin-right:1em;display:inline-block;width:-webkit-min-content;width:-moz-min-content;width:min-content;transition:color 0.2s, border-bottom-color 0.2s}@media screen and (max-width: 920px){li{padding:0.2em 0;width:100%;text-align:center;display:block}}li:hover{color:#F59B00}li:hover:after{transform:scaleX(1);background-color:#F59B00}li:after{display:block;content:'';height:0.2em;background-color:white;transform:scaleX(0);transition:transform 0.2s}

*{font-family:'KoPub Dotum', fantasy}section{padding:5em 0}.about-container{display:flex;padding:2em;align-items:flex-start;justify-content:center}@media screen and (max-width: 920px){.about-container{flex-direction:column;align-items:center}.about-container img{width:80%}}article{padding:8em 2em}.main-about{text-align:right;font-size:3vw;width:100%}@media screen and (max-width: 920px){.main-about{text-align:center;font-size:1.5rem}}@media screen and (min-width: 1920px){.main-about{font-size:3rem}}.sub-about{margin-top:5em;font-size:1.5vw;border-left:solid #05232E 0.5em}.sub-about>div{margin-left:1em;text-align:left}@media screen and (max-width: 920px){.sub-about>div{text-align:center;margin:0.5em}}@media screen and (min-width: 1920px){.sub-about{font-size:1.5rem}}@media screen and (max-width: 920px){.sub-about{font-size:1rem;border:solid #05232E 0.2em}}

*{font-family:'KoPub Dotum', fantasy}.title{text-align:center}.title__in{font-size:4rem;display:inline;border-bottom:solid #05232E 0.15em;font-weight:bold;color:#05232E}

